export const validation = {
      validateEmail(value: string) {
            let error
            if (!value) {
                  error = ' email   '
            } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)) {
                  error = '  '
            }
            return error
      },
      validatePass(value: string) {
            let error
            if (!value) {
                  error = ' password   '
            }
            return error
      },
}